.logo{
    height: 3rem;
    width: 10rem;
}
.header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    
}
.header-menu{
    list-style-type: none;
    display: flex;
    gap: 2rem;
    color: white;
}

.header-menu>li:hover{
    cursor: pointer;
    color: orange
}

@media screen and (max-width: 768px) {
    .header>:nth-child(2){
        position: fixed;
        right: 2rem;
        z-index: 99;
        top: 1.5rem;
    }
    .header-menu{
        flex-direction: column;
        background: var(--appColor);
        padding: 2rem;
        /* left: 0; */
    }
}