.programs-header {
    display: flex;
    gap: 5rem;
    font-weight: bold;
    font-size: 3rem;
    justify-content: center;
    text-transform: uppercase;
    color: white;
    font-style: italic;
    align-items: center;
}
.plans-blur-1{
    height: 20rem;
    width: 50rem;
    left: 0rem;
}
.plans-blur-2{
    height: 20rem;
    width: 50rem;
    right: 0;
    bottom: -125rem;
}

.plans-container{
    margin-top: 4rem;
    padding: 0rem 2rem;
    display: flex;
    flex-direction: column;
    gap: 4rem;
}
.plans{
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 3rem;
}
.plan{
    color: white;
    display: flex;
    flex-direction: column;
    background: var(--caloryCard);
    gap:2rem;
    padding: 1.5rem;
    width: 16rem;
}

.plans>:nth-child(2){
    background: var(--planCard);
    transform: scale(1.1);
}
.plans>:nth-child(2)>svg{
    fill: white;
}

.plan>svg{
    fill: var(--orange);
    width: 2rem;
    height: 2rem;
}

.plan>:nth-child(2){
    font-size: 1rem;
    font-weight: bold;
}

.plan>:nth-child(3){
    font-size: 3rem;
    font-weight: bold;
}
.plan>:nth-child(5){
    font-size: 0.8rem;
}
.features{
    display: flex;
    flex-direction: column;
    gap: 1rem;
}
.feature{
    display: flex;
    align-items: center;
    gap: 1rem;
}
.feature>img{
    width: 1.5rem;
}

@media screen and (max-width: 768px) {
    .plans{
        flex-direction: column;
        
    }
    .plans>:nth-child(2){
        transform: none;
    }
}